import React, { useState, useEffect } from "react";
import { useAuthContext } from "../../hooks/useAuthContext";
import { projectFirestore } from "../../firebase/config";
import "./AddressCard.css";
import { type } from "@testing-library/user-event/dist/type";

export default function Addresses() {
  const [addresses, setAddresses] = useState([]);

  const [title, setTitle] = useState("");
  const [address, setAddress] = useState("");
  const [nameSurname, setNameSurname] = useState("");

  const [pageState, setPageState] = useState(0);
  console.log("AddressCard");
  const { user } = useAuthContext();

  useEffect(() => {
    const getAddresses = async () => {
      //-----------------------
      const doc = await projectFirestore
        .collection("users")
        .doc(user.uid)
        .collection(addresses)
        .get();
      //-----------------------
      if (!doc.exists) {
        console.log("No such document!");
      } else {
        console.log("Document data:", doc.data().addresses);
      }
      setAddresses(doc.data().addresses);
    };
    getAddresses();
  }, []);

  const handlesubmit = async (e) => {
    e.preventDefault();

    console.log("handle on submit");
    try {
      await projectFirestore
        .collection("users")
        .doc(user.uid + "addresses")
        .add({
          type: type,
          namesurname: nameSurname,
          address: address,
        });
      console.log("handle on update end");
      window.location.href = "/" + user.displayName + "/" + "Addresses";
      return true;
    } catch (err) {
      console.log("Add Address Error: ", err.message);
      return false;
    }
  };

  return (
    <>
      {pageState === 0 && (
        <div>
          <h1 align="center">Addresses</h1>
          {addresses.map((element) => {
            return (
              <div className="wrapper-address">
                <div>
                  <h4>{element.title}</h4>
                  <h3>{element.content}</h3>
                </div>
              </div>
            );
          })}
          <button
            onClick={(e) => {
              setPageState(1);
            }}
            className="btnwide"
          >
            Add Address
          </button>
        </div>
      )}
      {pageState === 1 && (
        <div className="formwrap">
          <h4 align="center">Add Address</h4>
          <form onSubmit={handlesubmit}>
            <p>
              Address Title
              <input
                type="text"
                onChange={(e) => setTitle(e.target.value)}
                value={title}
              ></input>
            </p>
            <p>
              Address
              <textarea
                className="addressInput"
                onChange={(e) => setAddress(e.target.value)}
                value={address}
              ></textarea>
            </p>
            <p>
              Name Surname
              <input
                type="text"
                onChange={(e) => setNameSurname(e.target.value)}
                value={nameSurname}
              ></input>
            </p>
            <button className="btnwide">Add Address</button>
          </form>
        </div>
      )}
    </>
  );
}
